







import Vue from "vue";
import BudgetTableBox from "../../components/Page/BudgetTableBox.vue";
export default Vue.extend({
	components: {
		BudgetTableBox
	},
	data() {
		return {
			budgets: [] as any[]
		};
	},
	mounted() {
		this.$store
			.dispatch("GET_CLUB_BUDGETS")
			.then(budgets => {
				this.budgets = budgets.map((data:any)=>{
                    data.total = data.price * data.quantity + data.shipping
                    return data
                });
			})
			.catch(err => {});
	}
});
